import { _decorator, Component } from 'cc';
import { ESTATE, ganeshagold } from './cGaneshagold';
import { tween } from 'cc';
import { Label } from 'cc';
import { Vec3 } from 'cc';
import { Node } from 'cc';
import { Sprite } from 'cc';
import { SpriteFrame } from 'cc';
import { sp } from 'cc';
import { find } from 'cc';
import { ProgressBar } from 'cc';
import { Font } from 'cc';
import { Animation } from 'cc';
import { macro } from 'cc';
const { ccclass, property } = _decorator;


@ccclass('ganeshagoldBg')
export class ganeshagoldBg extends Component {
    @property({ type: Sprite, tooltip: "背景sprite" })
    public bg: Sprite = null!;
    @property({ type: [SpriteFrame], tooltip: "" })
    public bgs: SpriteFrame[] = [];
    @property({ type: Node, tooltip: "背光" })
    public Backlight: Node = null!;
    @property({ type: sp.Skeleton, tooltip: "象财神动画" })
    public ganesha: sp.Skeleton = null!;
    @property({ type: Animation, tooltip: "象财神动画" })
    public ganeshaAni: Animation = null!;
    @property({ type: Node, tooltip: "" })
    public fortuneMul: Node = null!;
    @property({ type: ProgressBar, tooltip: "" })
    public progress: ProgressBar = null!;
    @property({ type: Node, tooltip: "" })
    public barEffect: Node = null!;
    @property({ type: Label, tooltip: "" })
    public mul: Label = null!;
    @property({ type: Node, tooltip: "" })
    public mulAimNode: Node = null!;
    @property({ type: Node, tooltip: "" })
    public mulAimLight: Node = null!;
    @property({ type: Label, tooltip: "" })
    public mulAim: Label = null!;
    @property({ type: [Font], tooltip: "1为灰态" })
    public mulFont: Font[] = [];

    /**当前模式 */
    private _mod: EMOD;
    public get mod(): EMOD {
        return this._mod
    }
    public set mod(v: EMOD) {
        this._mod = v;
        ganeshagold.audio.playBgm(v == EMOD.F);
        this.fortuneMul.active = v == EMOD.F;
        if (this.fortuneMul.active) {
            this.initFortuneMul();
        }
        this.bg.spriteFrame = this.bgs[v];
        find("reel_n", this.node).active = v == EMOD.N;
        find("reel_f", this.node).active = v == EMOD.F;
        this.ganeshaIdle();
        this.Backlight.active = v == EMOD.F;
        if (ganeshagold.slotCtrl) ganeshagold.slotCtrl.rollPanel.active = v == EMOD.N;
    }

    protected update(dt: number): void {
        if (this.barEffect) this.barEffect.active =
            (this.progress.progress == 0 || this.progress.progress == 1) ? false : true;
    }

    onLoad() {

    }

    start() {
        this.mod = EMOD.N;
        this.ganeshaAni.on("finished" as any, () => {
            this.ganeshaAni.node.active = false;
            this.ganeshaIdle();
        });
        this.addListener();
        ganeshagold.bg = this;

    }

    private addListener() {
        this.ganesha.setCompleteListener((event) => {
            switch (event.animation.name) {
                case EGANESHAANI.win:
                case EGANESHAANI.win2:
                case EGANESHAANI.fastspinExit:
                    this.ganeshaIdle();
                    break;
                case EGANESHAANI.fastspinStart:
                    this.playAni(EGANESHAANI.fastspinIdle);
                    break;
            }
        })
    }

    private ganeshaIdle() {
        this.unscheduleAllCallbacks();
        this.ganesha.node.active = true;
        if (this.mod == EMOD.N) {
            this.schedule(this.radomNormalIdle, 10, macro.REPEAT_FOREVER);
        } else {
            this.schedule(this.radomFortuneIdle, 10, macro.REPEAT_FOREVER);
        }
    }
    private curMul: number = 0;
    private initFortuneMul() {
        this.progress.progress = 0;
        this.curMul = 0;
        this.mul.string = "X" + (this.curMul + 2);
        this.mulAimNode.active = false;
        this.mulAim.string = "X" + 0;
        this.mul.string = "X" + 2;
    }

    private progressMax: number = 3;
    private _progressIndex: number = 0;
    public get progressIndex(): number {
        return this._progressIndex
    }
    public set progressIndex(v: number) {
        this._progressIndex = v;
        if (this._progressIndex > 3) {
            this._progressIndex = 0;
            //倍数变更
        }
        tween(this.progress)
            .to(this._progressIndex * 0.3, { progress: 1 / this.progressMax * this._progressIndex })
            .delay(0.2)
            .call(() => {
                this.scheduleOnce(() => {
                    if (!this.mulAimNode.active) this.mulAimNode.active = true;
                    let str: string;
                    if (this.progressIndex == 3) {
                        this.progress.progress = 0;
                        str = this.mulAim.string;
                        str = str.substring(1);
                        this.mulAim.string = "X" + (Number(str) + 2);
                        str = this.mul.string;
                        str = str.substring(1);
                        this.mul.string = "X" + (Number(str) + 2);
                        this.scheduleOnce(() => {
                            this.doMul();
                        }, 0.3)
                    } else {
                        ganeshagold.main.flyBack();
                    }
                }, 0.3)

            })
            .start();
    }

    private aimMul: number;
    private mulProgress: number;
    private bindMulBn: boolean = false;
    bindMul(mul: number, mulProgress: number) {
        if (this.bindMulBn) return;
        this.bindMulBn = true;
        this.scheduleOnce(() => {
            this.bindMulBn = false;
        }, 1)
        this.aimMul = mul;
        this.mulProgress = mulProgress;
        this.doMul();
    }

    private doMul() {
        let str: string = this.mulAim.string;
        str = str.substring(1);
        let curMul: number = Number(str);
        if (curMul == this.aimMul) {
            this.progressIndex = this.mulProgress;
        } else {
            this.progressIndex = 3;
        }
    }

    /**随机待机动画 */
    private radomNormalIdle() {
        let id: number = Math.ceil(Math.random() * 4);
        if (id == 0) id = 1;
        if (id == 1) {
            this.playAni(EGANESHAANI.idle);
        } else {
            this.playAni((EGANESHAANI.idle + id));
        }
    }

    /**随机待机动画 */
    private radomFortuneIdle() {
        let id: number = Math.ceil(Math.random() * 2);
        if (id == 0) id = 1;
        if (id == 1) {
            this.playAni(EGANESHAANI.bonusIdle);
        } else {
            this.playAni((EGANESHAANI.bonusIdle + id));
        }
    }

    /**随机赢钱动画 显示赢得金币时展示*/
    radomWin() {
        let id: number = Math.ceil(Math.random() * 2);
        if (id == 0) id = 1;
        if (id == 1) {
            this.playAni(EGANESHAANI.win, false);
        } else {
            this.playAni((EGANESHAANI.win + id), false);
        }
    }
    k

    fastspinStart() {
        this.playAni(EGANESHAANI.fastspinStart, false);
    }

    fastspinEnd() {
        this.playAni(EGANESHAANI.fastspinExit, false);
    }

    /**播放   freeStart = "ganeshaFreeStart",
    freeMul = "ganeshaFreeMul",
    superWin = "ganeshaSuperWin",
    magaWin = "ganeshaMagaWin",
    bigwinBack = "ganeshaBigwinBack"*/
    playGaneshaAni(str: string) {
        this.unscheduleAllCallbacks();
        this.ganesha.node.active = false;
        this.ganeshaAni.node.active = true;
        this.ganeshaAni.play(str);
    }

    /**播放动画 */
    protected playAni(aimStr: string, loopBn: boolean = true) {
        this.ganesha.setAnimation(0, aimStr, loopBn);
    }
    /**添加动画 */
    protected addAni(aimStr: string, loopBn: boolean = true) {
        this.ganesha.addAnimation(0, aimStr, loopBn);
    }
}

export enum EGANESHAANI {
    idle = "idle",
    idle2 = "idle2",
    idle3 = "idle3",
    idle4 = "idle4",
    win = "win",
    win2 = "win2",
    bonusIdle = "bonus_idle",
    bonusIdle2 = "bonus_idle2",
    bonusWin = "bonus_win",
    bonusWin2 = "bonus_win2",
    fastspinExit = "fastspin_exit",
    fastspinIdle = "fastspin_idle",
    fastspinStart = "fastspin_start",
    freeStart = "ganeshaFreeStart",
    freeMul = "ganeshaFreeMul",
    superWin = "ganeshaSuperWin",
    magaWin = "ganeshaMagaWin",
    bigwinBack = "ganeshaBigwinBack"
}


export enum EMOD {
    N,  //普通模式
    F   //财富模式
}
