import { Label } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { sp } from 'cc';
import { baseSymbol, ESHOWTYPE } from '../../module_basic/ui_slot/match3/baseSymbol';
import { Button } from 'cc';
import { ESYMBOLID, ganeshagoldSymbol } from './ganeshagoldSymbol';
import { ganeshagold } from './cGaneshagold';
const { ccclass, property } = _decorator;

const GAME_COMBINATIONS: string[][] = [
    ["5", "8", "15"],
    ["5", "8", "15"],
    ["8", "10", "30"],
    ["8", "10", "30"],
    ["10", "15", "45"],
    ["15", "30", "60"],
    ["20", "45", "90"],
    ["30", "60", "150"],
];
@ccclass('ganeshagoldDetail')
export class ganeshagoldDetail extends Component {
    @property({ type: Node, tooltip: "背景板" })
    public bg: Node = null!;
    @property({ type: ganeshagoldSymbol, tooltip: "icons" })
    public symbol: ganeshagoldSymbol = null!;
    @property({ type: Node, tooltip: "nums" })
    public numNode: Node = null!;
    @property({ type: [Label], tooltip: "中奖分数" })
    public nums: Label[] = [];
    @property({ type: Node, tooltip: "specialNode" })
    public specialNode: Node = null!;

    init(symbol: ganeshagoldSymbol) {
        let symbolId: number = symbol.symbolId;
        let reelIndex: number = symbol.reelDate.index;
        let idx: number = symbolId - 1;
        this.symbol.symbolId = symbolId;
        this.symbol.showType = ESHOWTYPE.ske;
        this.symbol.playDetail();
        if (symbolId == ESYMBOLID.wild || symbolId == ESYMBOLID.scatter) {
            this.bg.w = 465;
            this.numNode.active = false;
            this.specialNode.active = true;
            this.specialNode.children[0].active = symbolId == ESYMBOLID.scatter;
            this.specialNode.children[1].active = !this.specialNode.children[0].active;
        } else {
            this.bg.w = 350;
            this.numNode.active = true;
            this.specialNode.active = false;
            let strs: string[] = GAME_COMBINATIONS[idx];
            for (let i = 0; i < strs.length; i++) {
                this.nums[i].string = strs[i];
            }
        }
        if (reelIndex <= 2) {
            this.bg.x = -85;
            this.bg.scale_x = 1;  
            this.numNode.x = 120;
            this.specialNode.x = 220;
        } else {
            this.bg.x = 85;
            this.bg.scale_x = -1;
            this.numNode.x = -210;
            this.specialNode.x = -220;
        }
    }

    onClick() {
        this.node.active = false;
        ganeshagold.reel.dark.active = false;
    }   

}