import { _decorator, Component, AudioClip } from 'cc';
import { ModuleDef } from '../../scripts/ModuleDef';
const { ccclass, property } = _decorator;

@ccclass('ganeshagoldAudio')
export class ganeshagoldAudio extends Component {
    onLoad() {
    }

    start() {
    }

    private urlArr: string[] = ["sound/bgm_mg", "sound/bgm_fs_main"];
    private fortuneBn: boolean;
    playBgm(fortuneBn: boolean) {
        if (this.fortuneBn == fortuneBn) return;
        this.fortuneBn = fortuneBn;
        tgx.AudioMgr.inst.audio.stopMusic();
        if(fortuneBn){
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_fs_intro, ModuleDef.SLOTS_GANESHAGOLD,()=>{
                tgx.AudioMgr.inst.audio.playMusicLoop(this.urlArr[1], ModuleDef.SLOTS_GANESHAGOLD)
            });
        }else{
            tgx.AudioMgr.inst.audio.playMusicLoop(this.urlArr[0], ModuleDef.SLOTS_GANESHAGOLD)
        }
        
    }

    /**滚动开始 */
    playReelLoop() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.spinBtn, ModuleDef.SLOTS_GANESHAGOLD);
    }
    /**滚动结束 */
    private playReelEndBn: boolean = false;
    playReelEnd(isSpeed: boolean = false) {
        if (this.playReelEndBn) return;
        this.playReelEndBn = true
        this.scheduleOnce(() => {
            this.playReelEndBn = false;
        }, 0.01)
        if (isSpeed) {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.spinStopQuick, ModuleDef.SLOTS_GANESHAGOLD);
        } else {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.spinStop, ModuleDef.SLOTS_GANESHAGOLD);
        }
    }
    /**spawn wild */
    playSpawnWild() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.symWild, ModuleDef.SLOTS_GANESHAGOLD);
    }
    /**spawn Scatter */
    playSpawnScatter() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.symScatter, ModuleDef.SLOTS_GANESHAGOLD);
    }

    /**bigwin */
    private bigwinId: number;
    playBigwin() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_bigwin_main, ModuleDef.SLOTS_GANESHAGOLD).then((value) => {
            this.bigwinId = value;
        });
    }
    /**bigwin end */
    private bigwinEndId: number;
    playBigwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.bigwinId, EEFFECT.bgm_bigwin_main, ModuleDef.SLOTS_GANESHAGOLD);
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_bigwin_end, ModuleDef.SLOTS_GANESHAGOLD).then((value) => {
            this.bigwinEndId = value;
        });
    }
    stopBigwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.bigwinEndId, EEFFECT.bgm_bigwin_end, ModuleDef.SLOTS_GANESHAGOLD);
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_bigwin_out, ModuleDef.SLOTS_GANESHAGOLD);
    }
    /**totalwin end */
    private totalId: number;
    playTotalwin() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_totalwin_main, ModuleDef.SLOTS_GANESHAGOLD).then((value) => {
            this.totalId = value;
        });
    }
    /**totalwin end */
    private totalEndId: number;
    playTotalwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.totalId, EEFFECT.bgm_totalwin_main, ModuleDef.SLOTS_GANESHAGOLD);
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_totalwin_end, ModuleDef.SLOTS_GANESHAGOLD).then((value) => {
            this.totalEndId = value;
        });
    }
    stopTotalwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.totalEndId, EEFFECT.bgm_totalwin_end, ModuleDef.SLOTS_GANESHAGOLD);
    }
}

export enum EEFFECT {
    bgm_fs_intro = "sound/bgm_fs_intro",//幸运模式背景音
    fastspinStart = "sound/sfxGame_extract/fastspinStart",//夺宝等待动画
    spinBtn = "sound/sfxGame_extract/spinBtn",//快速循环滚动
    spinStop = "sound/sfxGame_extract/spinStop",//普通滚动停止
    spinStopQuick = "sound/sfxGame_extract/spinStopQuick",//快速滚动停止
    symWild = "sound/sfxGame_extract/symWild",//spawn wild
    symScatter = "sound/sfxGame_extract/symScatter",//spawn scatter
    bgm_bigwin_main = "sound/bgm_bigwin_main",//bigwin
    bgm_bigwin_end = "sound/bgm_bigwin_end",//bigwin结束
    bgm_bigwin_out = "sound/bgm_bigwin_out",//bigwin退出
    bgm_totalwin_main = "sound/bgm_totalwin_main",//共赢得
    bgm_totalwin_end = "sound/bgm_totalwin_end",//共赢得结束
}
