import { _decorator, Component } from 'cc';
import { ganeshagold } from './cGaneshagold';
import { Node } from 'cc';
import { tween } from 'cc';
import { Label } from 'cc';
import { EMOD } from './ganeshagoldBg';
const { ccclass, property } = _decorator;


@ccclass('ganeshagoldFreeStart')
export class ganeshagoldFreeStart extends Component {
    @property({ type: Node, tooltip: "" })
    public progress: Node = null!;
    @property({ type: Node, tooltip: "" })
    public btn: Node = null!;
    protected start(): void {
        this.node.active = false;
        ganeshagold.freeStart = this;
        this.node.opacity = 0;
    }

    show() {
        this.node.active = true;
        this.showBtnBn = false;
        this.progress.opacity = 255;
        this.btn.opacity = 0;
        tween(this.node)
            .to(0.2, { opacity: 255 })
            .call(this.doShow, this)
            .start();
    }

    private get progressLab(): Label {
        return this.progress.getChildByName("lab").getComponent(Label);
    }
    private doShow() {
        ganeshagold.bg.mod = EMOD.F;
        let lab: Label = this.progressLab;
        lab.string = 0 + "";
        tween(lab)
            .to(0.5, { string: "100" })
            .call(() => {
                tween(this.progress)
                    .to(0.3, { opacity: 0 })
                    .start();
                tween(this.btn)
                    .to(0.3, { opacity: 255 })
                    .call(()=>{
                        this.showBtnBn = true;
                        this.scheduleOnce(this.hide,5);
                    })
                    .start();
            })
            .start();
    }

    private hide() {
        this.unschedule(this.hide);
        tween(this.node)
         .to(0.2, { opacity: 0 })
         .call(()=>{
            this.node.active = false;
            ganeshagold.main.freeStartBack();
         })
         .start();
    }

    private showBtnBn: boolean = false;
    private onHide() {
        if (this.showBtnBn) {
            this.hide();
        }
    }
}