import { _decorator, Component } from 'cc';
import { ESTATE, ganeshagold } from './cGaneshagold';
import { tween } from 'cc';
import { Label } from 'cc';
import { Vec3 } from 'cc';
import { Node } from 'cc';
import { RoomMgr } from '../../module_basic/scripts/RoomMgr';
import { UIHelp, UIhelpParam } from '../../module_basic/ui_help/UIHelp';
import { ApiReturn } from 'tsrpc-browser';
import { ResPlayMatch3 } from '../../module_basic/shared/protocols/public/slots/PtlPlayMatch3';
import { gameNet } from '../../module_basic/scripts/NetGameServer';
import { SubGameDef } from '../../module_basic/scripts/SubGameDef';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
import { dataChange } from './dataChange';
import { ganeshagoldSymbol } from './ganeshagoldSymbol';
import { reelInfo } from '../../module_slots_iceandfire/ts/dataChange';
import { EGANESHAANI, EMOD } from './ganeshagoldBg';
import { baseSymbol } from '../../module_basic/ui_slot/match3/baseSymbol';
import { instantiate } from 'cc';
import { Prefab } from 'cc';
import { Button } from 'cc';
import { ganeshagoldDetail } from './ganeshagoldDetail';
import { bezierMove } from '../../module_basic/utils/bezierMove';
import { ganeshagoldAudio } from './ganeshagoldAudio';
const { ccclass, property } = _decorator;


@ccclass('ganeshagoldMain')
export class ganeshagoldMain extends Component {
    @property({ type: Prefab, tooltip: "" })
    public detailPb: Prefab = null!;
    @property({ type: Prefab, tooltip: "" })
    public wildPb: Prefab = null!;
    public delayClick: boolean = false;
    onLoad() {

    }

    start() {
        ganeshagold.main = this;
        ganeshagold.audio = this.node.getComponent(ganeshagoldAudio);
    }

    onCLick(event: TouchEvent, args: any) {
        switch (args) {
            case "roll":
                this.startRoll();
                break;
            case "add":
                this.add();
                break;
            case "dec":
                this.dec();
                break;
            case "help":
                tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("rule"));
                break;
            case "pay":
                tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("payout"));
                break;
            case "lastmoney":
                ganeshagold.lastmoneyPanel.showPanel();
                break;
            case "exitGame":
                this.onBtnExitClicked();
                break;
        }
    }

    onBtnExitClicked() {
        tgx.UIAlert.show(LanguageData.getLangByID("exit_game"), true).onClick(async (ok: Boolean) => {
            if (ok) {
                ganeshagold.clear();
                RoomMgr.inst.exitRoom();
            }
        });
    }

    startRoll() {
        if (this.delayClick || ganeshagold.rollEnable == false) {
            return;
        }
        if (!ganeshagold.auto) {
            this.delayClick = true;
            this.scheduleOnce(() => {
                this.delayClick = false;
            }, 1);
            if (ganeshagold.status == ESTATE.stop) {
                ganeshagold.status = ESTATE.start;
                this.sendRoll();
            } else if (ganeshagold.status == ESTATE.run) {
                ganeshagold.slotCtrl.setSpinAnim(3);
                this.stopImmediately(ganeshagold.result.symbols, () => { this.rollCallBack() });
            }
        }
    }

    add() {
        if (ganeshagold.auto) {
            return;
        }
        ganeshagold.sumIdx += 1;
        ganeshagold.sumIdx = ganeshagold.slotCtrl.updateBet(ganeshagold.sumIdx) == 0 ? ganeshagold.sumIdx - 1 : ganeshagold.sumIdx;
    }

    dec() {
        if (ganeshagold.auto) {
            return;
        }
        ganeshagold.sumIdx -= 1;
        ganeshagold.sumIdx = ganeshagold.slotCtrl.updateBet(ganeshagold.sumIdx) == 0 ? ganeshagold.sumIdx + 1 : ganeshagold.sumIdx;
    }

    startAuto(n: any) {
        if (this.delayClick) {
            return;
        }
        if (ganeshagold.status == ESTATE.stop) {
            ganeshagold.auto = true;
            ganeshagold.autoTimes = n;
            // (((ganeshagold.slotCtrl.Btn_stopAuto as Node).children[0] as Node).getComponent(Label) as Label).string = n + "";
            (((ganeshagold.slotCtrl.Btn_stopAuto as Node).children[1] as Node).getComponent(Label) as Label).string = n + "";
            if (n > 999) {
                ((ganeshagold.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(0.76, 0.76, 0.76);
                ((ganeshagold.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(0.76, 0.76, 0.76);
            } else if (n > 99) {
                ((ganeshagold.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(0.9, 0.9, 0.9);
                ((ganeshagold.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(0.9, 0.9, 0.9);
            }
            else {
                ((ganeshagold.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(1, 1, 1);
                ((ganeshagold.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(1, 1, 1);
            }
            ganeshagold.slotCtrl.Btn_stopAuto.active = ganeshagold.auto;
            ganeshagold.slotCtrl.Btn_start.active = !ganeshagold.slotCtrl.Btn_stopAuto.active;
            this.scheduleOnce(this.sendRoll, 1.5);
        }
    }

    stopAuto() {
        ganeshagold.auto = false;
        ganeshagold.autoTimes = 0;
    }

    private rollCallBackBn: boolean = false;
    rollCallBack() {
        ganeshagold.status = 0;
        if (ganeshagold.status == 0) {
            if (this.rollCallBackBn) {
                return;
            }
            this.rollCallBackBn = true;
            this.scheduleOnce(() => {
                this.rollCallBackBn = false;
            }, 1)
            ganeshagold.reel.removeRadom();
            if (ganeshagold.result.winCoin > 0) {
                ganeshagold.slotCtrl.setSpinAnim(3);
            } else {
                ganeshagold.slotCtrl.setSpinAnim(2);
            }
            this.scheduleOnce(() => {
                this.rollCallBackNext();
            }, 0.3);
        }
    }

    /**滚动完成后操作 */
    private rollCallBackNext() {
        if (ganeshagold.result.removeItems.length > 0) {
            this.showWin();
        } else {
            if (ganeshagold.fortuneBn) {
                let num: number = ganeshagold.reel.flyWild();
                if (num == 0) {
                    this.doNext();
                }
            } else {
                this.doNext();
            }

        }
    }

    private showWin() {
        ganeshagold.reel.removeUnuseAll();
        let symbols: ganeshagoldSymbol[] = ganeshagold.reel.getSymbols as ganeshagoldSymbol[];
        let removes: number[] = ganeshagold.result.removeItems;
        for (let i = 0; i < removes.length; i++) {
            let removeIndex: number = removes[i];
            symbols[removeIndex].playWin();
        }
        if (ganeshagold.fortuneBn) {
            let num: number = ganeshagold.reel.flyWild();
            if (num == 0) {
                this.flyBack();
            } else {
                if (ganeshagold.result.winCoin > 0) ganeshagold.infoBoard.playAnimWin(ganeshagold.result.winCoin / ganeshagold.result.mul);
            }
        } else {
            this.scheduleOnce(this.settlement, 1);
        }
    }

    flyBack() {
        if (ganeshagold.result.winCoin > 0) {
            let flyMul: Node = instantiate(ganeshagold.bg.mulAim.node);
            flyMul.parent = this.node;
            flyMul.setSiblingIndex(this.node.children.length - 2);
            flyMul.position = ganeshagold.bg.mulAim.node.position;
            flyMul.y += ganeshagold.bg.mulAim.node.parent.y + ganeshagold.bg.mulAim.node.parent.parent.y;
            let bz: bezierMove = flyMul.addComponent(bezierMove);
            let p0: Vec3 = bz.node.position;
            let p3: Vec3 = ganeshagold.infoBoard.node.position;
            let p1: Vec3 = new Vec3(-100, p0.y);
            let p2: Vec3 = new Vec3(-100, p3.y);
            bz.pArr = [p0, p1, p2, p3];
            tween(bz)
                .to(0.3, { t: 1 })
                .call(() => {
                    bz.node.destroy();
                    this.settlement();
                })
                .start();
        } else {
            this.doNext();
        }
    }

    private settlement() {
        if (ganeshagold.result.winCoin > 0) {
            if (ganeshagold.result.winCoin < 5 * ganeshagold.betSum) {
                ganeshagold.infoBoard.playAnimWin(ganeshagold.result.winCoin, true);
                this.scheduleOnce(this.doNext, 2);
            } else {
                ganeshagold.bigwin.bigwincoin(ganeshagold.result.winCoin);
            }
        } else {
            this.doNext();
        }
    }

    closeBigWin() {
        ganeshagold.infoBoard.playAnimWin(ganeshagold.result.winCoin);
        this.doNext();
    }

    private doNext() {
        if (ganeshagold.resultIndex == 0 && ganeshagold.lotteryRes.results.length > 1) {
            ganeshagold.freeStart.show();
        } else {
            this.addResultIndex();
        }
    }

    private addResultIndex() {
        ganeshagold.resultIndex++;
        if (ganeshagold.resultIndex < ganeshagold.lotteryRes.results.length) {
            ganeshagold.slotCtrl.freeTimes = ganeshagold.result.leftFreeCount - 1;
            this.scheduleOnce(() => {
                ganeshagold.infoBoard.showInfosBn = true;
                ganeshagold.reel.loop();
                this.scheduleOnce(() => {
                    ganeshagold.reel.loopEnd(ganeshagold.result.symbols, () => {
                        this.rollCallBack();
                    },)
                }, 1)
            }, 0.5);
        } else {
            if (ganeshagold.lotteryRes.results.length > 1) {
                ganeshagold.freeEnd.show(ganeshagold.lotteryRes.winCoin);
            } else {
                this.playRollOver();
            }
        }
    }

    freeStartBack() {
        ganeshagold.bg.playGaneshaAni(EGANESHAANI.freeStart);
        ganeshagold.bg.progressIndex = 3;
        this.addResultIndex();
    }
    freeEndBack() {
        ganeshagold.bg.mod = EMOD.N;
        ganeshagold.slotCtrl.freeTimes = -1;
        this.playRollOver();
    }

    roll(list: number[]) {
        if (!ganeshagold.auto) {
            ganeshagold.rollEnable = true;
        }
        ganeshagold.status = 1;
        this.scheduleOnce(() => {
            ganeshagold.reel.loopEnd(list, () => { this.rollCallBack() });
        }, 0.5);
    }

    sendRoll() {
        this.initRoll();
        ganeshagold.autoTimes = ganeshagold.autoTimes > 0 ? ganeshagold.autoTimes - 1 : 0;
        if (ganeshagold.autoTimes == 0) {
            ganeshagold.auto = false;
        }
        ganeshagold.slotCtrl.setSpinAnim(1);
        let autoTimesLab: Label = ganeshagold.slotCtrl.Btn_stopAuto.children[1].getComponent(Label) as Label;
        autoTimesLab.string = ganeshagold.autoTimes + "";
        if (ganeshagold.autoTimes > 999) {
            ganeshagold.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(0.5, 0.5);
        } else if (ganeshagold.autoTimes > 99) {
            ganeshagold.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(0.64, 0.64);
        }
        else {
            ganeshagold.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(1, 1);
        }
        ganeshagold.reel.loop();
        this.sendLottery();
    }

    firstSymbolOver() {
        ganeshagold.reel.spawn();
        this.addDetailListener();
    }

    private addDetailListener() {
        let symbols: baseSymbol[] = ganeshagold.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            symbol.node.getComponent(Button).interactable = true;
        }
    }

    private removeDetailListener() {
        let symbols: baseSymbol[] = ganeshagold.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            symbol.node.getComponent(Button).interactable = false;
        }
        ganeshagold.reel.dark.active = false;
        let detail: ganeshagoldDetail = ganeshagold.detail;
        if (detail) detail.node.active = false;
    }

    addDetail(s: baseSymbol) {
        let detail: ganeshagoldDetail = ganeshagold.detail;
        if (!detail) {
            detail = instantiate(this.detailPb).getComponent(ganeshagoldDetail);
            detail.node.parent = s.node.parent.parent;
            ganeshagold.detail = detail;
        }
        detail.node.active = true;
        detail.node.position = s.node.position;
        detail.init(s as ganeshagoldSymbol);
        ganeshagold.reel.dark.active = true;
    }

    /**每次滚动初始化场景 */
    initRoll() {
        this.removeDetailListener();
        ganeshagold.infoBoard.showInfosBn = true;
    }

    /**单次滚动完成结算 */
    playRollOver() {
        this.rollComplete();
        this.doAuto();
    }

    doAuto() {
        if (ganeshagold.auto && ganeshagold.autoTimes > 0) {
            this.sendRoll();
        }
    }
    /**滚动完成 */
    private rollComplete() {
        ganeshagold.status = 0;
        ganeshagold.slotCtrl.setSpinAnim(0);
        ganeshagold.reel.spawn();
        this.addDetailListener();
    }

    async sendLottery() {
        let retSpin: ApiReturn<ResPlayMatch3> = await gameNet.PlayMatch3(SubGameDef.SLOTS_GANESHAGOLD, ganeshagold.betSum + "");
        this.lotteryBack(retSpin);
    }

    private lotteryBack(data: any) {
        if (data && data.isSucc) {
            console.log(data.res);
            this.exchange(data.res as ResPlayMatch3);
            console.log(ganeshagold.lotteryRes);
            ganeshagold.resultIndex = 0;
            if (ganeshagold.slotCtrl.isSpeed) {
                this.roll(ganeshagold.result.symbols)
            } else {
                this.scheduleOnce(() => {
                    this.roll(ganeshagold.result.symbols)
                }, 1)
            }
        }
    }

    exchange(data: ResPlayMatch3) {
        let res = dataChange.change(data);
        ganeshagold.lotteryRes = res;
    }

    stopImmediately(arr: number[], callBack: Function = () => { }) {
        if (!ganeshagold.auto) {
            ganeshagold.reel.stopImmediately(arr, callBack)
        }
    }
}
