import { SpriteFrame } from 'cc';
import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { Label } from 'cc';
import { Color } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { macro } from 'cc';
import { Prefab } from 'cc';
import { instantiate } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { Animation } from 'cc';
import { ganeshagold } from './cGaneshagold';
import { labNumShow } from '../../module_basic/utils/labNumShow';
const { ccclass, property } = _decorator;

@ccclass('ganeshagoldInfoBoard')
export class ganeshagoldInfoBoard extends Component {
    @property({ type: [Node], tooltip: "底板背景" })
    public bgs: Node[] = [];
    @property({ type: Node, tooltip: "消息跑马灯" })
    public infos: Node = null!;
    @property({ type: Node, tooltip: "赢钱信息" })
    public infoWin: Node = null!;

    onLoad() {
        ganeshagold.infoBoard = this;
    }

    protected start(): void {

    }

    private _lev: number = 0;
    public get lev(): number {
        return this._lev
    }
    public set lev(v: number) {
        this._lev = v;
        this.bgs[0].active = v == 0;
        this.bgs[1].active = v == 1;
    }

    /**winNode label */
    playAnimWin(coin: number, playBn: boolean = false, showTwBn: boolean = false) {
        this.showInfosBn = false;
        this.showTwBn = showTwBn;
        if (coin >= ganeshagold.betSum * 5) {
            this.lev = 1;
        } else {
            this.lev = 0;
        }
        if (playBn) {
            this.winLab.setRealNum(0);
            tween(this.winLab)
                .to(0.5, { num: coin * 100 })
                .start();
        } else {
            this.winLab.setRealNum(coin);
        }
    }

    /**是否显示跑马灯 */
    private _showInfosBn: boolean = false;
    public get showInfosBn(): boolean {
        return this._showInfosBn
    }
    public set showInfosBn(v: boolean) {
        this._showInfosBn = v;
        this.infos.active = v;
        this.infoWin.active = !v;
    }

    /**是否显示共赢得 */
    private _showTwBn: boolean = false;
    public get showTwBn(): boolean {
        return this._showTwBn;
    }
    public set showTwBn(v: boolean) {
        this._showTwBn = v;
        this.infoWin.getChildByName("win_info").active = !v;
        this.infoWin.getChildByName("totalwin_info").active = v;
    }

    private get winLab(): labNumShow {
        return this.infoWin.getChildByName("lab").getComponent(labNumShow);
    }
}