import { SubGameDef } from "../../module_basic/scripts/SubGameDef";
import { autoPanel } from "./autoPanel";
import { betPanel } from "./betPanel";
import { clientRes, dataChange, Result } from "./dataChange";
import { ganeshagoldAudio } from "./ganeshagoldAudio";
import { ganeshagoldBg } from "./ganeshagoldBg";
import { ganeshagoldBigwin } from "./ganeshagoldBigwin";
import { ganeshagoldDetail } from "./ganeshagoldDetail";
import { ganeshagoldFreeEnd } from "./ganeshagoldFreeEnd";
import { ganeshagoldFreeStart } from "./ganeshagoldFreeStart";
import { ganeshagoldInfoBoard } from "./ganeshagoldInfoBoard";
import { ganeshagoldMain } from "./ganeshagoldMain";
import { ganeshagoldReel } from "./ganeshagoldReel";
import { ganeshagoldSlotCtrl } from "./ganeshagoldSlotCtrl";
import { lastmoneyPanel } from "./lastmoneyPanel";

class cGaneshagold {
    public static _instance: cGaneshagold | null = null;

    public static get instance(): cGaneshagold {
        if (!this._instance) {
            this._instance = new cGaneshagold();
        }
        return this._instance;
    }

    public BETNUM = dataChange.BETNUM;
    public LINES = dataChange.LINES;
    public BET = dataChange.BET;

    private _isNewUser: boolean = false;
    public get isNewUser(): boolean {
        this._isNewUser = false;
        return this._isNewUser;
    }

    private _money: number = 0;
    public get money(): number {
        return this._money;
    }
    public set money(m: number) {
        this._money = m;
    }

    private _exchangeRate: number = 0;
    public get exchangeRate(): number {
        this._exchangeRate = 1;
        return this._exchangeRate;
    }

    public main: ganeshagoldMain = null!;
    public audio: ganeshagoldAudio= null!;
    public slotCtrl: ganeshagoldSlotCtrl = null!;
    public detail: ganeshagoldDetail = null!;
    public bet: number = 0;
    public betNum: number = 0;
    public sumIdx: number = 0;
    public freeStart: ganeshagoldFreeStart = null!;
    public freeEnd: ganeshagoldFreeEnd = null!;
    /**累计下注计算 */
    private _betSum: number;

    public get betSum(): number {
        if (!this._betSum) {
            this._betSum = Number(localStorage.getItem(SubGameDef.SLOTS_DRAGONHATCH + "betSum"));
        }
        return this._betSum;
    }

    public set betSum(v: number) {
        if (this._betSum != v) {
            localStorage.setItem(SubGameDef.SLOTS_DRAGONHATCH + "betSum", v + "");
        }
        this._betSum = v;
    }

    /**获奖信息 */
    public lotteryRes: clientRes = null!;

    /**自动状态 */
    public auto: boolean = false;
    /**自动次数 */
    public autoTimes: number = 0;
    /**转动状态0：停止，1：转动，2：开始转 */
    public status: ESTATE = 0;
    /**摇奖计数 */
    public rollIndex: number = 0;
    public _rollEnable: boolean = true;
    public set rollEnable(v: boolean) {
        this._rollEnable = v;
    }

    public get rollEnable(): boolean {
        return this._rollEnable;
    }
    public bg: ganeshagoldBg = null!;
    public reel: ganeshagoldReel = null!
    public infoBoard: ganeshagoldInfoBoard = null!;
    public bigwin: ganeshagoldBigwin = null!;
    /**下注界面 */
    public betPanel: betPanel = null!;
    /**自动界面 */
    public autoPanel: autoPanel = null!;
    /**余额界面 */
    public lastmoneyPanel: lastmoneyPanel = null!;

    /**摇奖次数index */
    private _resultIndex: number = 0;
    public get resultIndex(): number {
        return this._resultIndex
    }
    public set resultIndex(v: number) {
        this._resultIndex = v;
    }
    /**摇奖次数数据 */
    public get result(): Result {
        return this.lotteryRes.results[this.resultIndex];
    }

    private _fortuneBn: boolean = false;

    public get fortuneBn(): boolean {
        return this.resultIndex>0;
    }

    constructor() {

    }

    clear() {
        this.main.node.destroy();
        this.rollEnable = true;
        this.status = ESTATE.stop;
        this.slotCtrl = undefined;
        this.reel = undefined;
        this.bg = undefined;
        this.infoBoard = undefined;
        this.bigwin = undefined;
        this.detail = undefined;
    }
}

export let ganeshagold = cGaneshagold.instance;

export enum ESTATE {
    stop,
    run,
    start
}