import { _decorator, Component, macro, sp } from 'cc';
import { Node } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { LanguageSprite } from 'db://assets/module_basic/scripts/language/LanguageSprite';
import { Vec3 } from 'cc';
import { Label } from 'cc';
import { Animation } from 'cc';
import { SpriteFrame } from 'cc';
import { Sprite } from 'cc';

import { tween } from 'cc';
import { ganeshagold } from './cGaneshagold';
import { bezierMove } from '../../module_basic/utils/bezierMove';
import { NodePool } from 'cc';
import { instantiate } from 'cc';
import { Prefab } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ganeshagoldBigwin')
export class ganeshagoldBigwin extends Component {
    @property({ type: Prefab, tooltip: "" })
    public coinPb: Prefab = null!;
    @property({ type: Node, tooltip: "" })
    public coins: Node = null!;
    @property({ type: Node, tooltip: "" })
    public flower1: Node = null!;
    @property({ type: Node, tooltip: "" })
    public flower2: Node = null!;
    @property({ type: Node, tooltip: "" })
    public flower3: Node = null!;
    @property({ type: Node, tooltip: "" })
    public light: Node = null!;
    @property({ type: Label, tooltip: "coin label" })
    public coinLabel: Label = null!;
    @property({ type: LanguageSprite, tooltip: "" })
    public bigWinBw: LanguageSprite = null!;

    onLoad() {
    }
    protected start(): void {
        ganeshagold.bigwin = this;
        this.init();
    }
    private init() {
        this.coin = 0;
        this.coinLabel.string = "0";
        this.node.active = false;
        this.light.scale = new Vec3(1, 1);
    }

    //大奖数字滚动动画
    private aimCoin: number = 0;
    private addcoin: number = 0;
    private addOnce: number = 0;
    private _playOverBn: boolean = true;
    public get playOverBn(): boolean {
        return this._playOverBn;
    }
    public set playOverBn(v: boolean) {
        this._playOverBn = v;
    }

    private _coin: number = 0;
    public get coin(): number {
        return this._coin;
    }
    public set coin(v: number) {
        this._coin = v;
    }

    bigwincoin(coin: number) {
        ganeshagold.audio.playBigwin();
        this.node.active = true;
        this.coin = coin;
        this.playBigWinCoin();
    }

    private playBigWinCoin() {
        let coin = this.coin;
        this.node.active = true;
        this.bigBn = false;
        this.superBn = false;
        this.megaBn = false;
        this.aimCoin = coin;
        this.addcoin = 0;
        this.addOnce = this.aimCoin / 100;
        this.playOverBn = false;
        this.coinLabel.string = Num.exchangeToThousands(ganeshagold.exchangeRate, this.addcoin);
        this.schedule(this.coinOneChange, 0.05, macro.REPEAT_FOREVER);
        this.scheduleOnce(this.closeBigWin, 16);
    }

    private bigBn: boolean = false;
    private superBn: boolean = false;
    private megaBn: boolean = false;
    private coinOneChange() {
        this.addcoin += this.addOnce;
        if (this.addcoin >= this.aimCoin) {
            this.playBigWin(this.aimCoin);
            this.playCoinOver();
        } else {
            this.playBigWin(this.addcoin);
            this.coinLabel.string = Num.exchangeToThousands(ganeshagold.exchangeRate, this.addcoin);
        }
    }

    private playCoinOver() {
        this.unschedule(this.doCreate);
        this.unschedule(this.coinOneChange);
        ganeshagold.audio.playBigwinEnd();
        this.playOverBn = true;
        this.coinLabel.string = Num.exchangeToThousands(ganeshagold.exchangeRate, this.aimCoin);
        this.unschedule(this.closeBigWin);
        this.scheduleOnce(this.closeBigWin, 5);
    }

    private playBigWin(coin: number) {
        if (coin >= ganeshagold.betSum * 35 && this.megaBn == false) {
            this.playAnim_MegaWin();
            this.megaBn = true;
        } else if (coin >= ganeshagold.betSum * 15 && coin < ganeshagold.betSum * 35 && this.superBn == false) {
            this.playAnim_SuperWin();
            this.superBn = true;
        } else if (coin < ganeshagold.betSum * 15 && this.bigBn == false) {
            this.playAnim_BigWin();
            this.bigBn = true;
        }
    }

    onFastBigWin(e: TouchEvent) {
        if (this.playOverBn) {
            this.closeBigWin();
        } else {
            this.playCoinOver();
        }
    }

    //大奖BigWin动画
    private playAnim_BigWin() {
        this.bigWinBw.dataID = "win_bigwin";
        this.flower1.active = true;
        this.flower2.active = false;
        this.flower3.active = false;
    }

    //大奖SuperWin动画
    private playAnim_SuperWin(coin: number = 0) {
        this.bigWinBw.dataID = "win_megawin";
        this.flower1.active = false;
        this.flower2.active = true;
        this.flower3.active = false;
    }

    //大奖MegaWin动画
    private playAnim_MegaWin(coin: number = 0) {
        this.bigWinBw.dataID = "win_superwin";
        this.createCoin();
        this.light.scale = new Vec3(1.2, 1.2);
        this.flower1.active = false;
        this.flower2.active = true;
        this.flower3.active = true;
    }

    private maxNum: number = 50;
    private disTime: number = 0.5;
    private createCoin() {
        this.schedule(this.doCreate, this.disTime, macro.REPEAT_FOREVER);
    }

    private doCreate() {
        let num: number = Math.ceil(Math.random() * this.maxNum);
        for (let i = 0; i < num; i++) {
            let coin: Node = this.factory();
            coin.position = new Vec3(0, 0);
            coin.parent = this.coins;
            this.scheduleOnce(() => {
                this.coinMove(coin);
            }, Math.random() * this.disTime);
        }
    }

    private coinMove(coin: Node) {
        let radomSize: number = 1 + Math.random() * 0.6;
        let radomAngle: number = Math.random() * 360;
        coin.angle = radomAngle;
        coin.scale = new Vec3(radomSize, radomSize);
        let endSize:number = Math.random() * 0.4;
        let b: bezierMove = coin.getComponent(bezierMove);
        if (!b) b = coin.addComponent(bezierMove);
        let radomA:number = Math.random()*Math.PI*2;
        let p: Vec3 = new Vec3( 1000*Math.cos(radomA),1000*Math.sin(radomA));
        tween(coin)
            .to(2, { scale: new Vec3(endSize,endSize), position:p})
            .call(() => {
                this.recover(b.node);
            })
            .start();
    }

    private _pool: NodePool;
    private get pool(): NodePool {
        if (!this._pool) this._pool = new NodePool();
        return this._pool;
    }

    /**生产金币 */
    private factory(): Node {
        let coin: Node = this.pool.get();
        if (!coin) {
            coin = instantiate(this.coinPb);
        }
        return coin;
    }

    /**回收 */
    private recover(coin: Node) {
        this.pool.put(coin);
    }

    /**回收金币容器中的金币 */
    private clearCoins() {
        for (let i = this.coins.children.length - 1; i >= 0; i--) {
            let coin: Node = this.coins.children[i];
            this.recover(coin);
        }
    }

    closeBigWin() {
        this.unschedule(this.closeBigWin);
        ganeshagold.audio.stopBigwinEnd();
        ganeshagold.main.closeBigWin();
        if (this.node.active) {
            // ganeshagold.main.audio.stopbigwinmain();
            this.init();
        }
    }
}