import { _decorator, Component, Node, Prefab, sp, Sprite, SpriteFrame } from 'cc';
import { baseSymbol, ESHOWTYPE } from '../../module_basic/ui_slot/match3/baseSymbol';
import { ganeshagold } from './cGaneshagold';
import { instantiate } from 'cc';
import { tween } from 'cc';
import { bezierMove } from '../../module_basic/utils/bezierMove';
import { Vec3 } from 'cc';


const { ccclass, property } = _decorator;

@ccclass('ganeshagoldSymbol')
export class ganeshagoldSymbol extends baseSymbol {
    @property({ type: Node, tooltip: "选中态" })
    public select: Node = null!;
    onLoad() {

    }

    protected start(): void {
        this.selectBn = false;
    }

    private _selectBn: boolean = false;
    public get selectBn(): boolean {
        return this._selectBn;
    }
    public set selectBn(v: boolean) {
        this._selectBn = v;
        this.select.active = v;
    }

    /**滚动停止调用 播放wild出场动画*/
    idle() {
        let ske: sp.SkeletonData = this.skes[this.symbolId - 1];
        if (!ske) return;
        this.showType = ESHOWTYPE.ske;
        this.playAni(ESYMBOLANI.idle, true);
    }
    flyWild(): number {
        if (this.symbolId == ESYMBOLID.wild) {
            let bz: bezierMove = instantiate(ganeshagold.main.wildPb).getComponent(bezierMove);
            bz.node.parent = ganeshagold.main.node;
            bz.node.setSiblingIndex(ganeshagold.main.node.children.length - 2);
            bz.node.position = this.node.position;
            bz.node.y += this.node.parent.parent.y;
            let p0: Vec3 = bz.node.position;
            let p1: Vec3 = new Vec3(0, p0.y);
            let p2: Vec3 = ganeshagold.infoBoard.node.position;
            bz.pArr = [p0, p1, p2];
            tween(bz)
                .to(0.3, { t: 1 })
                .call(() => {
                    bz.node.destroy();
                    ganeshagold.bg.bindMul(ganeshagold.result.mul,ganeshagold.result.mulProgress);
                })
                .start();
            return 1;
        }
        return 0;
    }
    /**播放中奖 */
    playWin() {
        if (this.skes[this.symbolId - 1]) {
            this.showType = ESHOWTYPE.ske;
            this.playAni(ESYMBOLANI.win, true);
        } else {
            this.showType = ESHOWTYPE.normal;
        }
        this.selectBn = true;
    }

    playDetail() {
        if (this.skes[this.symbolId - 1]) {
            this.showType = ESHOWTYPE.ske;
            if (this.symbolId != ESYMBOLID.wild && this.symbolId != ESYMBOLID.scatter) {
                this.playAni(ESYMBOLANI.win, true);
            } else {
                this.playAni(ESYMBOLANI.idle, true);
            }
        } else {
            this.showType = ESHOWTYPE.normal;
        }
    }

    onClick(e: TouchEvent) {
        ganeshagold.main.addDetail(this);
    }
}

export enum ESYMBOLID {
    null = 0,
    J = 1,
    Q = 2,
    K = 3,
    A = 4,
    powder = 5,
    flower = 6,
    diya = 7,
    lady = 8,
    scatter = 9,
    wild = 10
}

export enum ESYMBOLANI {
    win = "win",                  //中奖过场
    idle = "idle",                //普通待机  wild\scatter  
    idleFastpin = "idle_fastpin"  //scatter等待
}